@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300;400;700&display=swap);
/*=============== GOOGLE FONTS ===============*/
/*=============== VARIABLES CSS ===============*/
:root {
  --header-height: 3.5rem; /*56px*/

  /*========== Colors ==========*/
  /*Color mode HSL(hue, saturation, lightness)*/
  --hue: 152;
  --first-color: hsl(235deg 52% 33%);
  --first-color-alt: hsl(var(--hue), 24%, 28%);
  --first-color-light: hsl(var(--hue), 24%, 66%);
  --first-color-lighten: hsl(var(--hue), 24%, 92%);
  --title-color: hsl(var(--hue), 4%, 15%);
  --text-color: hsl(var(--hue), 4%, 35%);
  --text-color-light: hsl(var(--hue), 4%, 55%);
  --body-color: #fafafa;
  --container-color: #FFF;
  --main-color: rgb(207, 205, 209);
  --blue-color: hsl(235deg 52% 33%);
  --yellow-color: hsl(58deg 82% 46%);

  /*========== Font and typography ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --body-font: 'Poppins', sans-serif;
  --urban-font: 'Kalam', sans-serif;
  --big-font-size: 2rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;

  /*========== Font weight ==========*/
  --font-thin: 300;
  --font-medium: 500;
  --font-semi-bold: 600;

  /*========== Margenes Bottom ==========*/
  /*.5rem = 8px | 1rem = 16px ...*/
  --mb-0-5: .5rem;
  --mb-0-75: .75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
}

/* Responsive typography */
@media screen and (min-width: 968px) {
  :root {
    --big-font-size: 3.5rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

/*=============== BASE ===============*/
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html{
  scroll-behavior: smooth;
}

body,
button,
input,
textarea{
  font-family: 'Poppins', sans-serif;
  font-family: var(--body-font);
  font-size: .938rem;
  font-size: var(--normal-font-size);
}

body{
  margin: 3.5rem 0 0 0;
  margin: var(--header-height) 0 0 0;
  background-color: #fafafa;
  background-color: var(--body-color);
  color: hsl(152, 4%, 35%);
  color: var(--text-color);
  transition: .4s; /*For animation dark mode*/
}

button{
  cursor: pointer;
  border: none;
  outline: none;
}

h1,h2,h3{
  color: hsl(152, 4%, 15%);
  color: var(--title-color);
  font-weight: 600;
  font-weight: var(--font-semi-bold);
}

hr {
  height: 10px;
	border: 0;
	box-shadow: 0 10px 10px -10px #8c8b8b inset;
}

ul{
  list-style: none;
}

a{
  text-decoration: none;
}

img{
  max-width: 100%;
  height: auto;
}

/*=============== THEME ===============*/
/*========== Variables Dark theme ==========*/
body.dark-theme {
  --first-color-dark: hsl(var(--hue), 8%, 20%);
  --title-color: hsl(var(--hue), 4%, 95%);
  --text-color: hsl(var(--hue), 4%, 75%);
  --body-color: hsla(0, 0%, 12%, 1);
  --container-color: hsl(var(--hue), 8%, 16%);
  --main-color: hsla(270, 5%, 9%, 1);
}


/*========== Button Dark/Light ==========*/
.change-theme {
  color: hsl(152, 4%, 15%);
  color: var(--title-color);
  font-size: 1.15rem;
  cursor: pointer;
}

.nav__btns {
  display: inline-flex;
  align-items: center;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
}

/*========== 
Color changes in some parts of 
the website, in dark theme 
==========*/

.dark-theme .steps__bg,
.dark-theme .questions {
  background-color: var(--first-color-dark);
}

.dark-theme .product__circle {
  background-color: #FFF !important;
  background-color: var(--container-color) !important;
}

.dark-theme .scroll-header {
  box-shadow: 0 1px 4px hsla(152, 4%, 4%, .3);
  box-shadow: 0 1px 4px hsla(var(--hue), 4%, 4%, .3);
}


/*=============== REUSABLE CSS CLASSES ===============*/
.section{
  padding: 5.5rem 0 1rem;
}

.section__title,
.section__title-center{
  font-size: 1.5rem;
  font-size: var(--h1-font-size);
  margin-bottom: 2rem;
  margin-bottom: var(--mb-2);
  line-height: 140%;
}

.section__title-center{
  text-align: center;
}

.container{
  max-width: 968px;
  margin-left: 1.5rem;
  margin-left: var(--mb-1-5);
  margin-right: 1.5rem;
  margin-right: var(--mb-1-5);
}

.grid{
  display: grid;
}

.main{
  overflow: hidden; /*For animation*/
}

/*=============== BUTTONS ===============*/
.button {
  display: inline-block;
  background-color: hsl(235deg 52% 33%);
  background-color: var(--first-color);
  color: #FFF;
  padding: 1rem 1.75rem;
  border-radius: .5rem;
  font-weight: 500;
  font-weight: var(--font-medium);
  transition: .3s;
  width: -webkit-max-content ;
  width: -moz-max-content ;
  width: max-content ;
  place-content: center;
}

.button:hover {
  background-color: hsl(152, 24%, 28%);
  background-color: var(--first-color-alt);
}

.button__icon {
  transition: .3s;
}

.button:hover .button__icon {
  transform: translateX(.25rem);
}

.button--flex {
  display: inline-flex;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  align-items: center;
  grid-column-gap: .5rem;
  -webkit-column-gap: .5rem;
          column-gap: .5rem;
}

.button--link {
  color: hsl(235deg 52% 33%);
  color: var(--first-color);
  font-weight: 500;
  font-weight: var(--font-medium);
}

.button--link:hover .button__icon {
  transform: translateX(.25rem);
}

.normal--text {
  font-size: .938rem;
  font-size: var(--normal-font-size);
}

.small--text {
  font-size: .813rem;
  font-size: var(--small-font-size);
}

.flex--l {
  justify-self: flex-start;
}
.flex--r {
  justify-self: flex-end;
}

.shadow {
  -webkit-filter: drop-shadow(3px 3px 5px #111);
  filter: drop-shadow(3px 3px 5px #111);
}



/*=============== Form styles ===============*/


/*=============== SCROLL BAR ===============*/
::-webkit-scrollbar {
  width: 0.6rem;
  background: hsl(152, 4%, 53%);
  background: hsl(var(--hue), 4%, 53%);
}

::-webkit-scrollbar-thumb {
  background: hsl(152, 4%, 29%);
  background: hsl(var(--hue), 4%, 29%);
}


@media screen and (max-width: 320px) {
  .container {
    margin-left: 1rem;
    margin-left: var(--mb-1);
    margin-right: 1rem;
    margin-right: var(--mb-1);
  }
}

@media  screen and (min-width: 767px)  {
  body {
    margin: 0;
  }
}

/* For large devices */
@media screen and (min-width: 992px){
  .container{
    margin-left: auto;
    margin-right: auto;
  }

  .section{
    padding: 8rem 0 1rem;
  }
  .section__title,
  .section__title-center{
    font-size: 1.5rem;
    font-size: var(--h1-font-size);
  }

}




/*=============== HEADER ===============*/
.header {
    width: 100%;
    background-color: var(--main-color);
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    box-shadow: 0px 3px 5px 0px rgb(0 0 0 / 75%);
    transition: .4s; /*for animation dark*/
  }
  
  /*=============== NAV ===============*/
  .nav {
    height: var(--header-height);
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .nav__logo,
  .nav__toggle,
  .nav__close {
    color: var(--title-color);
  }
  .nav__content-userandlogo {
    order: 2;
    display: flex;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
  }
  
  .nav__logo {
    font-weight: 700;
    letter-spacing: -1px;
    display: inline-flex;
    align-items: center;
    grid-column-gap: .5rem;
    -webkit-column-gap: .5rem;
            column-gap: .5rem;
    transition: .3s;
    
  }

  .nav__logo-icon  {
    fill: var(--title-color);
    width: 20px;
  }

  .nav__logo:hover {
    color: rgb(151, 151, 151)  ;
    
  }
  
  /* .nav__content-userandlogo .nav__logo:hover ~ .nav__content-userandlogo .nav__logo-icon {
    fill: rgb(151, 151, 151) !important ;
  } */

  .nav__user {
    width: 2.3rem;
    height: 2.3rem;
    border-radius: 50%;
    outline: 3px solid var(--title-color);
  }

  .nav__user-img {
    width: 2.3rem;
    height: 2.3rem;
    border-radius: 50%;
    object-fit: cover;
  }

  .nav__user-dropdown {
    position: absolute;
    background-color: var(--main-color);
    top: 100%;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    max-width: 200px;
    z-index: 2;
    border: 1px solid rgba(0, 0, 0, 0.04);
    box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14);
    right: 50%;
    left: 50%;
  }

  .nav__user-content-username {
    display: flex;
    background-color: var(--first-color);
    color: var(--white);
    justify-content: center;
    padding: .3rem;
  }

  .nav__user-item {
    display: block;
    cursor: pointer;
    color: var(--title-color);
    padding: .4rem 1rem;
    font-size: var(--normal-font-size);
     font-weight: var(--font-semi-bold);
     border-bottom: 1px solid var(--title-color);
     text-align: center;
  }
  .nav__user-item:hover {
    background-color: var(--body-color);
  }

 
  .nav__user-item:last-child {
    border-bottom: none;
  }

  
  .nav__toggle {
    display: inline-flex;
    font-size: 1.25rem;
    cursor: pointer;
    order: 3;
  }
  
  @media screen and (max-width: 767px) {
    .nav__menu {
      position: fixed;
      background-color: var(--body-color);
      width: 60%;
      height: 100%;
      top: 0;
      right: -100%;
      /* box-shadow: -2px 0 4px hsla(var(--hue), 24%, 15%, .1); */
      padding: 4rem 0 0 3rem;
      border-radius: 1rem 0 0 1rem;
      transition: .3s;
      z-index: var(--z-fixed);
    }

  }
  
  .nav__close {
    font-size: 1.5rem;
    position: absolute;
    top: 1rem;
    right: 1.25rem;
    cursor: pointer;
  }
  
  .nav__list {
    display: flex;
    flex-direction: column;
    grid-row-gap: 1.5rem;
    row-gap: 1.5rem;
  }
  
  .nav__link {
    color: var(--title-color);
    font-weight: var(--font-medium);
  }
  
  .nav__link:hover {
    color: var(--first-color);
  }
  
  /* Show menu */
  .show-menu {
    right: 0;
  }
  
  /* Change background header */
  .scroll-header {
    box-shadow: 0 1px 4px hsla(var(--hue), 4%, 15%, .1);
  }
  
  /* Active link */
  
  .active-link {
    position: relative;
    color: var(--first-color);
  }
  
  .active-link::after {
    content: "";
    position: absolute;
    bottom: -.5rem;
    left: 0;
    width: 50%;
    height: 2px;
    background-color: var(--first-color);
  }



  @media  screen and (min-width: 767px)  {
    body {
      margin: 0;
    }
  
    .nav {
      height: var(--header-height );
      grid-column-gap: 3rem;
      -webkit-column-gap: 3rem;
              column-gap: 3rem;
      justify-content: space-around    ;
    }
  
    .nav__toggle,
    .nav__close {
      display: none;
    }
  
    .nav__list {
      flex-direction: row;
      grid-column-gap: 3rem;
      -webkit-column-gap: 3rem;
              column-gap: 3rem;
    }
  
    .nav__menu {
      margin-left: 0;
    }
    .nav__content-userandlogo {
      order: 1 !important;
    }
    .nav__btns {
      order: 2;
    }
    .nav__menu{
      order: 3;
    }

  }

  
/*=============== HOME ===============*/
.home {
  padding: 0;
}

.home__container {
  max-width: 968px;
  position: relative;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.home__container .swiperclass {
  max-width: 1000px;
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  justify-content: center;
  align-items: center;
  background: url(https://iurbanradio.tk/images/home/1641976326285graffiti-street.webp
  );
  background-repeat: no-repeat;
  background-size: 100% 100%;
  object-fit: cover !important;
  aspect-ratio: 16/9;
  padding: 8px 1rem;
  
}

.home__img {
  width: 200px;
  justify-self: center;
  line-height: 0;

  -webkit-filter: drop-shadow(3px 3px 0 black) drop-shadow(-3px -3px 0 black);
  filter: drop-shadow(3px 3px 0 black) drop-shadow(-3px -3px 0 black);
}

.home__container .swiperclass:nth-child(2n+1) .home__img  {
  transform: rotate(6deg);
}
.home__container .swiperclass:nth-child(2n) .home__img {
  transform: rotate(-6deg);
}

.home__data {
  padding: 1rem .6rem;
  position: relative;
  z-index: 5;
  font-family: var(--urban-font);
  color: #fff;
  -webkit-filter: drop-shadow(2px 2px 0 black) drop-shadow(-2px -2px 0 black);
  filter: drop-shadow(2px 2px 0 black) drop-shadow(-2px -2px 0 black);
}

/* .home__data::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: var(--body-color);
  opacity: 0.6;
  border: 2px solid var(--main-color);

  -webkit-box-shadow: 10px 10px 5px -6px rgba(0,0,0,0.75);
  -moz-box-shadow: 10px 10px 5px -6px rgba(0,0,0,0.75);
  box-shadow: 10px 10px 5px -6px rgba(0,0,0,0.75);
} */

.home__title {
  color: #fff;
  font-size: var(--big-font-size);
  line-height: 120%;
  margin-bottom: var(--mb-1);
}

.home__description {
  margin-bottom: var(--mb-1-5);
  font-size: var(--h3-font-size);
  font-weight: var(--font-medium);
  line-height: 100%;
  opacity: 1;

  -webkit-filter: drop-shadow(1px 1px 0 black) drop-shadow(-1px -1px 0 black);
  filter: drop-shadow(1px 1px 0 black) drop-shadow(-1px -1px 0 black);
}

.home__social {
  position: absolute;
  top: 2rem;
  right: -1rem;
  display: grid;
  justify-items: center;
  grid-row-gap: 3.5rem;
  row-gap: 3.5rem;
}

.home__social-follow {
  font-weight: var(--font-medium);
  font-size: var(--smaller-font-size);
  color: var(--title-color);
  position: relative;
  transform: rotate(90deg);
}

.home__social-follow::after {
  content: "";
  position: absolute;
  width: 1rem;
  height: 2px;
  background-color: var(--title-color);
  right: -45%;
  top: 50%;
}

.home__social-links {
  display: inline-flex;
  flex-direction: column;
  grid-row-gap: 0.25rem;
  row-gap: 0.25rem;
}

.home__social-link {
  font-size: 1rem;
  color: var(--title-color);
  transition: 0.3s;
}

.home__social-link:hover {
  transform: translateX(0.25rem);
}

/* Swiper Class */
.swiper-pagination {
  position: absolute;
  margin-top: var(--mb-1);
  bottom: 0 !important;
  left: 0;
  right: 0;
}

.swiper-pagination .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background-color: hsla(270, 5%, 9%, 1);
  opacity: 1;
  transition: all .4s ease-in-out;
  border-radius: 0 !important;
}

.swiper-pagination.swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 0.5rem;
}

.swiper-pagination .swiper-pagination-bullet-active {
  width: 1.5rem;
  height: 10px;
  border-radius: 0.5rem;
}

@media screen and (max-width: 320px) {
  .home {
    padding:  0;
  }
  .home__img {
    width: 180px;
  }

  .home__title {
    font-size: var(--h1-font-size);
  }
}

@media screen and (min-width: 767px) {
  .home__container {
    grid-template-columns: repeat(2, 1fr);
  }

  .home {
    padding: 0 0;
  }

  .home__container {
    align-items: center;
  }
  .home__img {
    width: 280px;
    order: 1;
  }

  .home__social {
    top: 30%;
  }
  .home__container .swiperclass {
    flex-direction: row;
  }
  .home__container .swiperclass.reverse {
    flex-direction: row-reverse;
    grid-gap: 3rem;
    gap: 3rem;
  }

  .home__container .home__data {
    flex-direction: column;
  }
}

/* For large devices */
@media screen and (min-width: 992px) {
  .home {
    padding: 0;
  }
  .home__img {
    width: 350px;
  }
  .home__description {
    padding-right: 7rem;
  }

  .swiper-slide {
    padding: 0 2rem;
  }
}

@media screen and (min-width: 1200px) {
  .home__social {
    right: -3rem;
    grid-row-gap: 4.5rem;
    row-gap: 4.5rem;
  }
  .home__social-follow {
    font-size: var(--small-font-size);
  }
  .home__social-follow::after {
    width: 1.5rem;
    right: -60%;
  }
  .home__social-link {
    font-size: 1.15rem;
  }
}

.container__spinner {
  width: 100%;
  min-height: 50vh;
  display: grid;
  place-content: center;
}


.container__spinner .box__spin {
  animation:
    bounce alternate infinite
    cubic-bezier(.2, .65, .6, 1);
}
.container__spinner .box__spin.one {
  --bounce-offset: -20px;
  animation-duration: 400ms;
}
.container__spinner .box__spin.two {
  --bounce-offset: -30px;
  animation-duration: 500ms;
}
.container__spinner .box__spin.three {
  --bounce-offset: -40px;
  animation-duration: 600ms;
}
.container__spinner section {
  display: flex;
  grid-gap: 16px;
  gap: 16px;
}
.box__spin {
  width: 50px;
  height: 50px;
  background: var(--blue-color);
  padding: 8px;
  display: grid;
  place-content: center;
  color: white;
  text-align: center;
  border-radius: 50%;
}


@keyframes bounce {
  from {
    transform: translateY(0px);
  }
  to {
    transform: translateY(
      var(--bounce-offset)
    );
  }
}

.post__container {
  display: grid;
  margin-top: 2rem;
}

.posts__content {
  grid-template-columns: 1fr;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  padding-top: 1rem;
  
}

.post__section-title {
  justify-self: center;
 
}

.post__content {
  position: relative;
  background: var(--blue-color);
  border-radius: 5px;
  text-align: center;
  overflow: hidden;
  cursor: pointer;
}
.description__post {
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  min-height: 20%;
  padding: 1rem 0;
  background-color: #1e1e1e3b;
  bottom: 0;
}

.post__img {
  width: 100%;
  transition: .3s;
  aspect-ratio: 16 / 9;
  object-fit: contain;
}

.post__title {
  color: #fff;
  transition: .3s;
}
.post__content:hover .post__img,.post__content:hover .post__title {
  transform: translateY(-.5rem);
}

/* pagination */
.pagination__content {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 2rem 0;
}


.pgn__btn-next, .pgn__btn-pre {
  display: inline-flex;
  width: 2.3rem;
  height: 2.3rem;
  margin: 0 .3rem;
  justify-content: center;
  align-items: center;
  background-color: var(--main-color);
  color: var(--title-color);
}

.pgn__btn-aa, .pgn__btn-number {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 2.3rem;
  height: 2.3rem;
  font-weight: 600;
}
.pgn__btn-aa {
  background-color: var(--blue-color);
  color: var(--white);
}

.pgn__btn-number {
  background-color: var(--main-color);
  color: var(--title-color);
}

.hidden {
  display: none;
}

@media  screen and (min-width: 767px)  {
  .posts__content {
    grid-template-columns: repeat(2,1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
    padding-top: 1rem;
  }
  
}

