  position: absolute;
  background: rgb(10 10 10 / 66%);
  height: 52px;
  
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  border: 2px solid rgba(255, 255, 255, 0.2);
  border-bottom-color: rgba(255, 255, 255, 0.116);
  border-left: rgba(255, 255, 255, 0.116);
  border-right: rgba(255, 255, 255, 0.116);
