  top: -50px;
  left: 0;
  right: 0;
}



.slide-bottom {
	animation: slide-bottom 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
.slide-top {
	animation: slide-top 3s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

.audio-controls {
  display: flex;
  position: relative;
  justify-content: center;
  transition: 200ms all ease-out;
  left: 0;
  right: 0;
  bottom: -24px;
