  justify-content: space-evenly;
  background-color: var(--main-color);
  height: 32px;
  padding: 4px 0;
  box-shadow: 0px -3px 5px 0px rgba(0, 0, 0, 0.75);
  z-index: 1001;
}

.audio-controls .play svg,
.audio-controls .pause svg {
  height: 25px;
  width: 25px;
  padding: 3px;
  position: absolute;
  z-index: 1050;
  background-color: var(--main-color);
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.75);
}
.audio-controls .loading svg {
  height: 30px;
  width: 30px;
  padding: 3px;
  position: absolute;
  z-index: 1050;
  background-color: var(--main-color);
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
 border-radius: 50%;
 border: 2px solid var(--title-color);
 transition:2s;
}

.audio-controls .play svg {
  width: 45px;
  height: 45px;
  top: -33px;
  background: var(--main-color);
  padding-left: 7px;
  padding-top: 3px;
  border-radius: 50%;
  margin: auto;
  border: 5px solid var(--blue-color);
  margin-left: -24px;
}
.audio-controls .play path {
  fill: var(--title-color) !important;
}

.audio-controls button path {
  fill: var(--title-color) ;
}
.button-player {
  width: 100%;
  background-color: transparent;
}
.audio-controls span {
  color: var(--title-color);
  font-family: roboto, arial;
  font-weight: 600;
}
.audio-controls span > svg {
