}

/*
 * ----------------------------------------
 * animation slide-bottom
 * ----------------------------------------
 */
@keyframes slide-bottom {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(50px);
  }
}




/*
 * ----------------------------------------
 * animation slide-top
 * ----------------------------------------
 */
@keyframes slide-top {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100px);
  }
}


@keyframes slide-lid-out {
  from {
    transform: translateY(0);
  }
  to {
    transform: translateY(-100%);
  }
}
@keyframes background-scroll {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 0 100%;
  }
}
@keyframes wave1 {
  0% {
    height: 10%;
  }
  33% {
    height: 30%;
  }
  66% {
    height: 15%;
  }
  100% {
    height: 35%;
  }
}
@keyframes wave2 {
  0% {
    height: 40%;
  }
  33% {
    height: 20%;
  }
  66% {
    height: 40%;
  }
  100% {
    height: 20%;
  }
}
@keyframes wave3 {
  0% {
    height: 45%;
  }
  33% {
    height: 20%;
  }
  66% {
    height: 30%;
  }
  100% {
    height: 15%;
  }
}
@keyframes wave4 {
  0% {
    height: 30%;
  }
  33% {
    height: 25%;
  }
  66% {
    height: 45%;
  }
  100% {
    height: 25%;
  }
}
@keyframes wave5 {
  0% {
    height: 20%;
  }
  33% {
    height: 35%;
  }
  66% {
    height: 15%;
  }
  100% {
    height: 35%;
  }
}
@keyframes stopwaves {
  100% {
    height: 3px;
  }
}

