.itemhomesetting__checkbox input[type=checkbox] {
    height: 0;
    width: 0;
    visibility: hidden;
    
  }
  
  .itemhomesetting__checkbox label {
    cursor: pointer;
    text-indent: -9999px;
    width: 2.2rem;
    height: 1rem;
    background: grey;
    display: block;
    border-radius: 100px;
    position: relative;
    display: inline-block;
  }
  
  .itemhomesetting__checkbox label:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 1rem;
    height: 1rem;
    background: #fff;
    border-radius: 90px;
    transition: 0.3s;
  }
  
  .itemhomesetting__checkbox input:checked + label {
    background: #22ff35;
  }
  
  .itemhomesetting__checkbox input:checked + label:after {
    left: calc(100% - 0px);
    transform: translateX(-100%);
  }
  
  .itemhomesetting__checkbox label:active:after {
    width: 2rem;
  }

  .itemhomesetting__content-bnt-file {
      position: relative;
      display: inline-block;
      max-width: 250px;
  }


  .file-select::before {
